import { Box, Divider, Typography } from "@mui/material";
import React from "react";
import PlayerCard from "../UI/Card/PlayerCard";
import { team } from "../../static/team/team";

const TeamPage = () => {

    return (
        <Box component="main">
            <Box className="flex-row jcc wfull">
                <Typography variant="h2" sx={{ my: 3 }}>
                    
                </Typography>    
            </Box>
            <Box className="block wfull" sx={{ mt: 3 }}>
                <Box className="flex-column gap-2 wfull">
                    <Box className="flex-column gap-1">
                        <Typography variant="h4">
                            
                        </Typography>  
                        <Box className="flex-row gap-1 wrap">
                            {Object.keys(team.FORWARDS).map(el => (
                                <PlayerCard 
                                    name={team.FORWARDS[el][0]}
                                    number={team.FORWARDS[el][1]}
                                    key={Math.random()}
                                />
                            ))}
                        </Box>
                    </Box>
                    <Box className="flex-column gap-1">
                        <Typography variant="h4">
                            
                        </Typography>  
                        <Box className="flex-row gap-1 wrap">
                            {Object.keys(team.MIDFIELDERS).map(el => (
                                <PlayerCard 
                                    name={team.MIDFIELDERS[el][0]}
                                    number={team.MIDFIELDERS[el][1]}
                                    key={Math.random()}
                                />
                            ))}
                        </Box>
                    </Box>
                    <Box className="flex-column gap-1">
                        <Typography variant="h4">
                            
                        </Typography>  
                        <Box className="flex-row gap-1 wrap">
                            {Object.keys(team.DEFENDERS).map(el => (
                                <PlayerCard 
                                    name={team.DEFENDERS[el][0]}
                                    number={team.DEFENDERS[el][1]}
                                    key={Math.random()}
                                />
                            ))}
                        </Box>
                    </Box>
                    <Box className="flex-column gap-1">
                        <Typography variant="h4">
                            
                        </Typography>  
                        <Box className="flex-row gap-1 wrap">
                            {Object.keys(team.GOALKEEPERS).map(el => (
                                <PlayerCard 
                                    name={team.GOALKEEPERS[el][0]}
                                    number={team.GOALKEEPERS[el][1]}
                                    key={Math.random()}
                                />
                            ))}
                        </Box>
                    </Box>
                    <Divider flexItem />
                </Box>
            </Box>
        </Box>
    )
}

export default TeamPage;