import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";

const MediaPage = () => {

    return (
        <Box component="main">
            <Box className="block wfull" sx={{ mt: 3 }}>
                <Typography variant="h4">
                        
                </Typography>
                <Box className="flex-column" sx={{ mt: 2 }}>
                    <Button 
                        onClick={() => window.location = 'https://vk.com/public215422697'}
                        variant="contained" 
                        disableElevation
                    >
                        
                    </Button>
                </Box>
            </Box>
            <Box className="block wfull" sx={{ mt: 3 }}>
                <Typography variant="h4">
                    
                </Typography>
                <Box className="flex-column" sx={{ mt: 2 }}>
                    <Typography variant="body1" className="wfull flex-row jcc note">
                          
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default MediaPage;