import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";

const NewsCard = ({date, title, desc}) => {

    return (
        <Box className="flex-column gap-1 block wfull newsCard">
            <Box className="flex-column">
                <Typography variant="h5" className="note_second">
                    {date}
                </Typography>
                <Typography variant="h4">
                    {title}
                </Typography>
            </Box>
            <Typography variant="body1" className="note" dangerouslySetInnerHTML={{ __html: desc }} />
        </Box>
    )
}

export default NewsCard;