import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

const FastCard = ({rival, date, goalScored, goalOmitted, aboutMatch}) => {

    return (
        <Box className="last_match_fastCard">
            <Box className="flex-row" sx={{ overflowX: 'hidden' }}>
                <Typography variant="h5" className="note">
                    vs&nbsp;
                </Typography>
                <Typography variant="h5">
                    {rival || 'Unknown'}
                </Typography> 
            </Box>
            <Typography variant="h5" className="note_second date_fastCard">
                {date || 'no date'}
            </Typography> 
            {/* <Typography variant="caption" className="note_second">
                
            </Typography>  */}
            <Box className="flex-column">
                <Box className="flex-row goals" sx={{ my: 1 }}>
                    {goalScored !== goalOmitted
                        ?   <>
                                {goalScored > goalOmitted
                                    ?   <>
                                            <Typography variant="h3" className="winner">
                                                {goalScored || 'NaN'}
                                            </Typography>
                                            <Typography variant="caption">
                                                &nbsp;:&nbsp;
                                            </Typography>
                                            <Typography variant="h3" className="loser">
                                                {goalOmitted || 'NaN'}
                                            </Typography> 
                                        </>
                                    :   <>
                                           <Typography variant="h3" className="loser">
                                                {goalScored || 'NaN'}
                                            </Typography>
                                            <Typography variant="caption">
                                                &nbsp;:&nbsp;
                                            </Typography>
                                            <Typography variant="h3" className="winner not_we">
                                                {goalOmitted || 'NaN'}
                                            </Typography> 
                                        </>
                                }
                            </>
                        :   <>
                                <Typography variant="h3">
                                    {goalScored || '?'}
                                </Typography>
                                <Typography variant="caption">
                                    &nbsp;:&nbsp;
                                </Typography>
                                <Typography variant="h3">
                                    {goalOmitted || '?'}
                                </Typography> 
                            </>
                    }
                </Box>
            </Box>
            <Button
                onClick={() => window.location = aboutMatch || null}
                variant="contained" 
                disableElevation
                sx={{ mb: 1 }}
                endIcon={<OpenInNewIcon className="white"/>}
            >
                
            </Button>
        </Box>
    )
}

export default FastCard;