import { Chip, IconButton, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useNavigate } from "react-router-dom";
import { MATCHES_ROUTE, MEDIA_ROUTE, NEWS_ROUTE, STARTPAGE_ROUTE, TEAM_ROUTE } from "../../routes/consts";
import HomeIcon from '@mui/icons-material/Home';
import ArrowOutwardIcon from '@mui/icons-material/ArrowOutward';
import NewspaperIcon from '@mui/icons-material/Newspaper';
import PermMediaIcon from '@mui/icons-material/PermMedia';
import GroupsIcon from '@mui/icons-material/Groups';
import ThemeSwitcher from "./ThemeSwitcher";

const HeaderOnPage = () => {
    const history = useNavigate();

    return (
        <Box component="header" className="flex-row jcsb gap-1 wfull">
            <Box className="flex-column gap-0">
                <Box className="flex-row title" onClick={() => history(STARTPAGE_ROUTE)}>
                    <Typography variant="h2" className="note">
                        &nbsp;
                    </Typography>
                    <Typography variant="h2">
                        
                    </Typography>
                </Box>
                <Box className="flex-row">
                    <Box className="greenColor" />
                    <Box className="redColor" />
                </Box>
                <Box className="flex-row gap-0 wrap headerChips">
                    <Tooltip title="" arrow>
                       <IconButton className="note" onClick={() => history(STARTPAGE_ROUTE)}>
                            <HomeIcon />
                        </IconButton> 
                    </Tooltip>
                    <ThemeSwitcher />
                    <Chip 
                        label="" 
                        className="chip-header" 
                        onClick={() => history(TEAM_ROUTE)} 
                        icon={<GroupsIcon sx={{ fontSize: 'medium' }} />}
                    />
                    <Chip 
                        label="" 
                        className="chip-header" 
                        onClick={() => history(MATCHES_ROUTE)} 
                        icon={<ArrowOutwardIcon sx={{ fontSize: 'medium' }}/>}
                    />
                    <Chip 
                        label="" 
                        className="chip-header" 
                        onClick={() => history(NEWS_ROUTE)} 
                        icon={<NewspaperIcon sx={{ fontSize: 'medium' }} />}
                    />
                    <Chip 
                        label="" 
                        className="chip-header" 
                        onClick={() => history(MEDIA_ROUTE)}
                        icon={<PermMediaIcon sx={{ fontSize: 'medium' }} />}
                    />
                </Box>
            </Box>
        </Box>
    )
}

export default HeaderOnPage;