import { Box, Typography } from "@mui/material";
import React from "react";
import MatchCard from "../UI/Card/MatchCard";
import { matches } from "../../static/team/matches";

const MatchesPage = () => {

    return (
        <Box component="main">
            <Typography variant="h2" sx={{ my: 3 }}>
                 
            </Typography>
            <Box className="wfull" sx={{ mt: 3 }}>
                <Box className="flex-column gap-1">
                    {Object.keys(matches).map(el => (
                        <MatchCard 
                            date={matches[el][1]}
                            rivary={matches[el][0]}
                            goalScored={matches[el][2]}
                            goalOmitted={matches[el][3]}
                            link={matches[el][4]}
                            key={Math.random()}
                        />
                    ))}
                </Box>
            </Box>
        </Box>
    )
}

export default MatchesPage;