import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";

const MatchCard = ({date, rivary, goalScored, goalOmitted, link}) => {

    return (
        <Box className="flex-column gap-2 wfull block">
            <Box className="flex-column gap-0 wfull">
                <Box className="flex-row wfull jcc gap-1 matchCardGoals">
                    <Box className="flex-row teammat jcfe">
                        <Typography variant="h4" className="note">
                            &nbsp;
                        </Typography>
                        <Typography variant="h4">
                            
                        </Typography>
                    </Box>
                    <Typography variant="h4">
                        VS
                    </Typography>
                    <Box className="flex-row teammat">
                        <Typography variant="h4" className="note">
                            &nbsp;
                        </Typography>
                        <Typography variant="h4">
                            {rivary}
                        </Typography>
                    </Box>
                </Box>
                <Box className="flex-row jcc wfull">
                    <Typography variant="h6" className="note">
                        {date}
                    </Typography>
                </Box>
            </Box>
            <Box className="flex-row wfull jcc">
                <Typography variant="h4">
                    {goalScored}
                </Typography>
                <Typography variant="h4">
                    &nbsp;:&nbsp;
                </Typography>
                <Typography variant="h4">
                    {goalOmitted}
                </Typography>
            </Box>
            <Box className="wfull flex-row jcc">
                <Button 
                    onClick={() => window.location = link || null}
                    variant="contained"
                    disableElevation
                >
                    
                </Button>
            </Box>
        </Box>
    )
}

export default MatchCard;