import { Box, Typography } from "@mui/material";
import React from "react";
import NewsCard from "../UI/Card/NewsCard";
import { news } from "../../static/news";

const NewsPage = () => {

    return (
        <Box component="main">
            <Typography variant="h2" sx={{ mt: 3 }}>
                
            </Typography>
            <Box className="flex-column gap-1 wfull" sx={{ mt: 1 }}>
                {Object.keys(news).reverse().map(el => (
                    <NewsCard 
                        date={news[el][0]}
                        title={news[el][1]}
                        desc={news[el][2]}
                        key={Math.random()}
                    />
                ))}
                {/* <Box className="header flex-column jcsb acc">
                    <Typography variant="h5" className="note">  </Typography>
                </Box> */}
            </Box>
        </Box>
    )
}

export default NewsPage;