import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import FastCard from "../Card/FastCard";
import { matches } from "../../../static/team/matches";

const LastMatchesCard = () => {

    return (
        <Box>
            <Box className="flex-row gap-3 wrap lastGames">
                <Typography variant="h3">
                     
                </Typography> 
                <Box className="flex-row jcfe gap-0" sx={{ mt: 1 }}>
                    {Object.keys(matches).map(el => (
                        <Box key={Math.random()}>
                            {matches[el][2] !== matches[el][3]
                                ?   <>
                                        {matches[el][2] > matches[el][3]
                                            ?   <Box className="lastGames-el LS-lose" key={Math.random()} />
                                            :   <Box className="lastGames-el LS-win" key={Math.random()} />
                                        }
                                    </>
                                :   <Box className="lastGames-el" />
                            }
                        </Box>
                    ))}
                </Box>
            </Box>
            <Box className="block_media" sx={{ mt: 3 }}>
                <Box className="flex-row gap-1" sx={{ overflowX: 'scroll' }}>
                    {Object.keys(matches).map(el => (
                        <FastCard 
                            rival={matches[el][0]}
                            date={matches[el][1]}
                            goalScored={matches[el][2]}
                            goalOmitted={matches[el][3]}
                            aboutMatch={matches[el][4]}
                            key={Math.random()}
                        />
                    ))}
                </Box>
            </Box>
        </Box>
    )
}

export default LastMatchesCard;