import React, { useState } from 'react';
import { BrowserRouter } from 'react-router-dom'
import AppRouter from './components/AppRouter';
import { Container } from '@mui/material';
import { appContext } from "./context/index";
import FooterOnPage from './components/UI/FooterOnPage';
import HeaderOnPage from './components/UI/HeaderOnPage';


const App = () => {
  
  const [theme, setTheme] = useState(true); // true - dark; false - light

  return (
    <appContext.Provider value={{
      theme, setTheme
    }}>
      <BrowserRouter>
        {!theme
          ? <style>:root {"{--font: 'Inter', sans-serif !important;--color: #000;--colorSecond: #6a6a6aa8;--colorOpacity: #000000e0;--colorInvert: #fff;--colorWhite: #fff;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #c4c3c6; --bgBody: #fff;--bgSecond: #f9f9f9;--bgSecondHelper: rgba(0, 0, 0, 0.06);}"}</style>
          : <style>:root {"{--font: 'Inter', sans-serif !important;--color: #fff;--colorSecond: #6a6a6aa8;--colorOpacity: #ffffffe0;--colorInvert: #000;--colorWhite: #fff;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #c4c3c6; --bgBody: #101010;--bgSecond: #181818;--bgSecondHelper: rgba(0, 0, 0, 0.75);}"}</style>
        }
        <Container maxWidth="lg">
          <HeaderOnPage />
          <AppRouter />
          <FooterOnPage />
        </Container>
      </BrowserRouter>
    </appContext.Provider>
  );
}

export default App;